import React, {useEffect, useState} from 'react'
import About1 from '../assets/images/about1.png'
import About2 from '../assets/images/about2.png'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


export default function About() {

	return (
		<div className='ScreenPage'>
			{/* <ReactCSSTransitionGroup 
				transitionAppear={true}
				transitionAppearTimeout={600}
				transitionEnterTimeout={600}
				transitionLeaveTimeout={600}
				transitionName={'FadeIn'}
				> */}
				<div className='container'>
					<div className='row'>
						<section className='basic-intro col-12'>
							<div data-aos="fade-right" className='float-left about-text-1 col-lg-6 col-xs-12'>
								<div>HELLO&nbsp;&nbsp; &gt;_</div>
								<div>I AM VISHAL</div>
								<div className='budding-programmar-text' style={{fontSize: 20, marginTop: 62}}>A BUDDING PROGRAMMER</div>
							</div>
							<img data-aos="fade-left" className='float-right col-lg-6 col-xs-12' src={About1} />
						</section>
						<section className='basic-intro col-12'>
							<img data-aos="fade-left" data-aos-delay="300" className='float-left col-xs-12 col-lg-6 d-none d-xl-block' src={About2} />
							<div data-aos="fade-right" data-aos-delay="300" className='float-right about-text-2 col-xs-12 col-xl-6'>
							I am an Intermediate Programmer who started coding 5 years ago when I was in Class IX. I can't say if I have learnt alot, but I gained alot of exposure overtime, which only time and practise could have provided. <br/><br/>
	Currently, I am in 2nd year of Computer Science Engineering looking forward to learn as much as I can. Coding is something I love alot from the very beginning when I started it. Nothing can be better than commanding a machine to do something for you. This very thing made me fall in love with it. ;)
							</div>
						</section>
					</div>
				</div>
				{/* </ReactCSSTransitionGroup> */}
		</div>
	)
}