import React from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import { experienceAndEducation } from '../data'
import ExperiencePath from '../components/experience/ExperiencePath'
import SideImage from '../assets/images/experience.png'

export default function Experience() {
    const experiencedata = experienceAndEducation.map((experienceElement) => {
        return (
            <ExperiencePath
                key={experienceElement.id}
                position={experienceElement.position}
                image={experienceElement.image} 
                organisation={experienceElement.organisation}
                duration={experienceElement.duration}
                location={experienceElement.location}
                startDate={experienceElement.startDate}
                endDate={experienceElement.EndDate}
            />
        )
    })
    return (
        <div className='ScreenPage'>
            {/* <ReactCSSTransitionGroup 
				transitionAppear={true}
				transitionAppearTimeout={600}
				transitionEnterTimeout={600}
				transitionLeaveTimeout={600}
				transitionName={'FadeIn'}
			> */}
            <h1 data-aos="fade-left" className='heading'>Experience and Education</h1>
            <div className='below-heading'>
                
            </div>
                {/* <iframe className='d-none d-xl-block experience-side-image' src="https://giphy.com/embed/xUA7bdpLxQhsSQdyog" width="480" height="360" frameBorder="0" class="giphy-embed" allowFullScreen></iframe><p></p> */}
                <img data-aos="fade-left" className='d-none d-xl-block experience-side-image' src={SideImage} />
            {experiencedata}

            {/* </ReactCSSTransitionGroup> */}
		</div>
    )
}