import './App.css';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import { useSpring, animated } from 'react-spring'
import { FcMenu } from 'react-icons/fc'
import { BsXCircleFill } from 'react-icons/bs'
import {useState, useEffect} from 'react'
import AOS from 'aos'
import 'aos/dist/aos.css';

import Nav from './Nav'
import About from './Screens/About'
import Contact from './Screens/Contact'
import Portfolio from './Screens/Portfolio'
import Awards from './Screens/Awards'
import Blog from './Screens/Blog'
import Experience from './Screens/Experience'
import Resume from './Screens/Resume'
import Skills from './Screens/Skills'




function App() {

  AOS.init()

  const [navbarClass, setNavbarClass] = useState(false)

  function toggleNavbar() {
    setNavbarClass(!navbarClass)
  }

  const sweepFromLeft = useSpring({marginLeft: 0, from: {marginLeft: -200}})
  // const op0to1 = useSpring({ config: {duration: 1000}, opacity: 1, from: {opacity: 0}})
  return (
    <Router>
      <div onClick={toggleNavbar} className='d-block d-lg-none hamburger-icon'>< FcMenu /></div>
      <div className="App">
        <animated.div style={sweepFromLeft} className={ navbarClass ? 'NavContainer NavbarOpenAnimation' : 'NavContainer NavbarCloseAnimation'}>
          <div onClick={toggleNavbar} className='d-block d-lg-none navbar-close'>< BsXCircleFill /></div>
          <Nav toggleNavbar={toggleNavbar} />
        </animated.div>
        <div className='ScreenContainer'>
          <Switch>
            <Route path ='/' exact component={About} />
            <Route path ='/contact' exact component={Contact} />
            <Route path ='/projects' exact component={Portfolio} />
            <Route path ='/awards' exact component={Awards} />
            <Route path ='/blog' exact component={Blog} />
            <Route path ='/experience' exact component={Experience} />
            <Route path ='/resume' exact component={Resume} />
            <Route path ='/skills' exact component={Skills} />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
