import React from 'react'

const ProjectBox = (props) => (
        <div className={props.class + " col-xs-12 col-md-6 col-lg-4"}>
            <div className="text-align-center card card-centered" style={{width: '100%'}}>
                <img style={{}} class="card-img-top projects-image" src={props.image} alt="Card image cap" />
                <div class="card-body">
                    <h5 class="card-title">{props.name}</h5>
                    <p class="card-text">{props.desc}</p>
                    <a href={props.link} class="btn btn-primary">Visit This</a>
                </div>
            </div>
        </div>
)

export default ProjectBox
