import React, {useState} from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {projects} from '../data'
import ProjectBox from '../components/portfolio/ProjectBox'

export default function Experience() {
    const [currentCat, setCurrentCat] = useState('All')

    let projectToDisp
    if(currentCat == 'All') {
        projectToDisp = projects.map(project => (
            <ProjectBox
                key={project.id}
                name={project.name} 
                tech={project.tech}
                image={project.image}
                date={project.date}
                class={'project-display-true'}
                desc={project.desc}
                link={project.link}
            />
        ))
    }
    else {
        projectToDisp = projects.map(project => (
            <ProjectBox 
                key={project.id}
                name={project.name} 
                tech={project.tech}
                image={project.image}
                date={project.date}
                class={ project.cat == currentCat ? 'project-display-true' : 'project-display-false'}
                desc={project.desc}
                link={project.link}
            />
        ))
    }
    console.log(projectToDisp)
    return (
        // <ReactCSSTransitionGroup 
        //     transitionAppear={true}
        //     transitionAppearTimeout={600}
        //     transitionEnterTimeout={600}
        //     transitionLeaveTimeout={600}
        //     transitionName={'FadeIn'}
        //     >
            <div className='ScreenPage'>
                <h1 data-aos="fade-left" className='heading'>My Projects</h1>
                <div data-aos="fade-right" className='below-heading'>
                    Below are my projects categorized by Popular, Latest and Upcoming.
                </div>
            
                <div className='container'>
                    <div data-aos="zoom-in" className='project-categories'>
                        <div onClick={() => setCurrentCat('All')} className={currentCat=='All' ? 'project-cat-active' : null}>
                            All
                        </div>
                        <div onClick={() => setCurrentCat('Popular')} className={currentCat=='Popular' ? 'project-cat-active' : null}>
                            Popular
                        </div>
                        <div onClick={() => setCurrentCat('Latest')} className={currentCat=='Latest' ? 'project-cat-active' : null}>
                            Latest
                        </div>
                        <div onClick={() => setCurrentCat('Upcoming')} className={currentCat=='Upcoming' ? 'project-cat-active' : null}>
                            Upcoming
                        </div>
                    </div>
                    <div className='row' style={{padding: '25px 35px'}}>
                        {projectToDisp}
                    </div>
                    {/* <div className=''>
                    <img align="center" height='180px' src="https://github-readme-stats.vercel.app/api?username=vishu1694&coount_private=true&hide_title=false&show_icons=true&include_all_commits=true&line_height=21&theme=radical" />
                    <br />
                    <img align="center" height='180px' src="https://github-readme-stats.vercel.app/api/top-langs/?username=vishu1694&hide_title=false&layout=compact&theme=radical" />
                    <br />
                    </div> */}

                </div>
		    </div>
        // </ReactCSSTransitionGroup>
    )
}