import React from 'react'
import {skills} from '../data'

const SkillsBox = (props) => {
    return (
        <div style={{flexDirection: 'column', marginBottom: 80}} className="d-flex align-items-center justify-content-center col-xs-6 col-sm-4 col-md-4 col-lg-2">
            <div className="skill-box">
                <img className='skill-image' src={props.image} />
            </div>
            <div className="skill-name text-align-center">{props.name}</div>
        </div>
    )
}

export default function Skills() {
    const skillArray = skills.map(skill => {
        console.log('Mapping');
        return <SkillsBox key={skill.id} name={skill.name} image={skill.image} />
    })
    return (
        <div className='skills-container'>
            <h1 data-aos="fade-left" className='heading'>My Skills</h1>
            <div data-aos="fade-right" className='below-heading'>
                I have been practising MERN Stack Web Development from quite some time now.
                Below are some of my best skills...
            </div>
            <div className="container">
                <div data-aos="zoom-out" className="row">
                    {skillArray}
                </div>
                <div style={{marginTop: -30, fontSize: 20}} className="text-align-center">
                    and many more...
                </div>
            </div>
        </div>
    )
}