import React from 'react'

function calculate_duration(StartDate, EndDate) {
    StartDate = new Date(StartDate)
    EndDate = EndDate=='Present' ? new Date() : new Date(EndDate)
    // const todayDate = Math.floor((new Date() - new Date('March, 2020'))/86400000)
    const durationInDay  = Math.floor((EndDate - StartDate)/86400000)
    let durationInMonth, durationInYear = 0
    let totalDuration = ""
    if(durationInDay > 30) {
        if(durationInDay>365) {
            durationInYear = Math.floor(durationInDay/365)
            totalDuration = totalDuration + String(durationInYear) + ' yr '
        }
        durationInMonth = Math.floor((durationInDay-(durationInYear*365))/30)
        totalDuration = totalDuration + String(durationInMonth) + ' months'
    }
    if(totalDuration === "") {
        totalDuration = `${durationInDay} days`
    }
    return totalDuration
}

export default function ExperiencePath(props) {
    return (
        <div data-aos="flip-up" data-aos-offset="0" data-aos-delay="200">
            <div className='row experience-element-holder'>
                <div className='circle-image-holder'>
                    <img className='circle-image' src={props.image} />
                    <div className='line'></div>
                </div>
                <div className='experience-info-holder'>
                    <div className='experience-position'>{props.position}</div>
                    <div className='experience-other-info'>{props.organisation}</div>
                    <div className='experience-other-info'>{`${props.startDate} - ${props.endDate}`}  ||  {calculate_duration(props.startDate, props.endDate)}</div>
                    <div className='experience-other-info'>{props.location}</div>
                </div>
            </div>
        </div>
    )
}